import { Component, OnInit } from '@angular/core';
import { WithdrawalService } from '../shared/services/withdrawal.service';

@Component({
  selector: 'app-withdrawal',
  templateUrl: './withdrawal.component.html',
  styleUrls: ['./withdrawal.component.css']
})
export class WithdrawalComponent implements OnInit {
  withdrawals:any[];
  constructor(private widService:WithdrawalService) { }

  ngOnInit() {

      this.widService.getWithdrawals().subscribe(
        res=>{
          this.withdrawals=res;
        },
        err=>{
          console.log(err);
        }
      )
  }

  setPaid(i){
    let wid=this.withdrawals[i];
    this.widService.updateWithdrawal(wid.id, {sendSms:true, smsType:'Paid'}, {state:'Paid', user:wid.user.id}).subscribe(
      res=>{
        this.withdrawals[i].state=res.state;
      },
      err=>{
        console.log(err);
      }
    );
  }

  setReversed(i){
    let conf=window.confirm("Are you sure to reverse?");
    if(!conf){
      return;
    }
    let wid=this.withdrawals[i];
    this.widService.updateWithdrawal(wid.id, {sendSms:true, smsType:'Reversed'}, {state:'Reversed', user:wid.user.id}).subscribe(
      res=>{
        this.withdrawals[i].state=res.state;
      },
      err=>{
        console.log(err);
      }
    );
  }

}



