import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { HttpClient } from '@angular/common/http';
import { AppConfig } from '../../app.config';

@Injectable()
export class WithdrawalService {

  constructor(private http: HttpClient) { }
  getWithdrawals(filter?:any): Observable<any[]> {
    return this.http.get<any[]>(AppConfig.BASICS.API_URL + "/admin/withdrawals");
  }

  updateWithdrawal(id:string, params:any, data:any): Observable<any> {
    return this.http.put<any>(AppConfig.BASICS.API_URL + "/admin/withdrawal/"+id, data, {params:params});
  }

}



